import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'noaudio'
})
export class NoaudioPipe implements PipeTransform {

  transform(audios: any): any {

    if(!audios){
      return 'assets/vacio.mp3';
    }

    if(audios.preview_url!=null){
      return audios.preview_url;
    }
    else{
      return 'assets/vacio.mp3';
    }
  }

}
